@import "partials/mixins";
@import "partials/colors";
@import "partials/grid";

/* Fonts */
@import url('https://fonts.googleapis.com/css?family=Raleway:400,700');

* {
    margin: 0 auto;
    padding: 0;
    font-family: sans-serif;
    box-sizing: border-box;
    color: #404040;
    font-family: "Raleway", Arial, sans-serif;
}
/*Nagłowek*/
header {
    background-color: #000;
    width: 100%;
    color: white;
    overflow: visible;
    position: relative;

    & .grid-container {
        overflow: visible;
    }
}

.logo {
    position: relative;
    display: inline-block;
    float: left;
    text-transform: uppercase;
    margin: 10px auto;
    font-weight: 400;
    color: white;

}
.logo > span {
    font-weight: bold;
    color: $mainColor;
}

nav {
    position: relative;
    float: right;
    width: 50%;
}
nav > ul {
        float: inherit;
        font-weight: 700;
        ul {
            top: 80%;
            position: absolute;
            visibility: hidden;
            opacity: 0;

            &:before{
                @include arrow('top', #000);
            }
            li{
                position: absolute;
                display: none;
            }
        }
        li{
            display: inline-block;
            list-style: none;
            color: white;
            background-color: black;
            text-align: center;
            padding: 0.625rem 0;
            position: relative;
            width: 6.250rem;

        }

        li:hover{
        background-color: $mainColor;
            ul{
                visibility: visible;
                opacity: 1;
                li {
                    display: inline-block;
                    // z-index: 2000;
                }
            }
        }
        li a {
            width: 7.5rem;
            text-decoration: none;
            color: inherit;
        }
}

